




























































































































import { Component, Mixins, Prop } from 'vue-property-decorator'
import PopupMixin from '@/mixins/popup'
import { PaginationInterface, IErrorRow } from '@/interface'
import ImportRepository from '@/services/repositories/import'
import getRepository from '@/services'
import BaseIcon from '@/components/base/icon/BaseIcon.vue'
import BaseTable from '@/components/base/table/BaseTable.vue'
import DistrictMixin from '@/mixins/commonMixin'

const importReq: ImportRepository = getRepository('import')

@Component({ components: { BaseTable, BaseIcon } })
export default class ImportFileDetail extends Mixins(PopupMixin, DistrictMixin) {
  data: Array<Record<string, any>> = []
  @Prop({ required: false, type: Object, default: {} }) detailFile!: Record<string, any>
  @Prop({ required: false, type: Boolean, default: false }) isError!: boolean

  detail: Record<string, any> = {}

  tableHeight: string | number = 'auto'

  get getRouteName(): boolean {
    return this.$route.name === 'importCbnv'
  }

  getHeightOfTable(): void {
    this.tableHeight = `${window.innerHeight - 123}px`
  }
  mounted(): void {
    this.getHeightOfTable()
  }
  errorRow: Array<IErrorRow> = []
  dataError = []
  totalErrorCount = 0

  sheetAactive: any = 'KHKD'

  isLoading = false
  isLoadingPopup = false

  query: PaginationInterface = {
    page: 1,
    limit: 20,
    total: 0
  }

  get getIndex(): number {
    return this.query.limit * (this.query.page - 1) + 1
  }

  get fileName(): string {
    if (this.detailFile && this.detailFile.fileName) {
      return this.detailFile.fileName.replaceAll('.xlsx', ' ')
    }
    return ''
  }

  // get errorCount(): number {
  //   const list = filter(this.errorRow, item => item.sheetName === this.sheetAactive)
  //   if (list.length) {
  //     return list[0].errorCount
  //   } else if (!list.length && this.sheetAactive) {
  //     return 0
  //   }
  //   return this.totalErrorCount
  // }

  handleOpen(): void {
    this.init()
  }

  init(isLoadingTable = false): void {
    if (isLoadingTable) {
      this.isLoading = true
    } else {
      this.isLoadingPopup = true
    }
    const sheetName = this.isError ? null : this.$route.name === 'importCbnv' ? 'CTTD_CBNV' : 'CTTD_DVKD'

    const params = {
      ...this.query,
      total: null,
      fileId: this.detailFile.id,
      sheetName
    }
    importReq
      .getDetail(this.districtId, this.isError, params)
      .then(res => {
        if (!this.isError) {
          this.detail = res.total
          this.data = res.list
          this.query.total = res.total?.numOfRow
        } else {
          this.dataError = res.list
          this.totalErrorCount = res.total
          this.query.total = res.total
        }
        if (isLoadingTable) {
          this.isLoading = false
        } else {
          this.isLoadingPopup = false
        }
      })
      .catch(e => {
        if (isLoadingTable) {
          this.isLoading = false
        } else {
          this.isLoadingPopup = false
        }
        this.$message.error(e.message)
      })
  }

  resetQuery(): void {
    this.query = {
      page: 1,
      limit: 20,
      total: 0
    }
  }

  handleClose(): void {
    this.detail = {}
    this.dataError = []
    this.errorRow = []
    this.resetQuery()
  }
  handleSizeChange(size: number): void {
    this.query.limit = size
    this.init(true)
  }
  handleCurrentChange(page: number): void {
    this.query.page = page

    this.init(true)
  }
}
