








































































import { Component, Vue, Watch } from 'vue-property-decorator'
import BaseIcon from '@/components/base/icon/BaseIcon.vue'
import EmployeeRepository from '@/services/repositories/employee'
import getRepository from '@/services'
import { trim } from 'lodash'

const employeeReq: EmployeeRepository = getRepository('employee', {
  districtId: 14
})
@Component({ components: { BaseIcon } })
export default class FilterImport extends Vue {
  members: Array<Record<string, unknown>> = []
  filter: Record<string, any> = {
    userId: [],
    dateStart: null,
    dateEnd: null,
    status: [],
    search: ''
  }
  queryUser = {
    i: 0,
    ps: 20,
    groupCode: 'INSURANCE',
    statusAccount: 3,
    search: ''
  }

  @Watch('$route.name') handleRouteNameChange(): void {
    this.members = []
    this.filter = {
      userId: [],
      dateStart: null,
      dateEnd: null,
      status: [],
      search: ''
    }
    this.resetQuery()
  }

  get dateOption(): any {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const self = this
    return {
      disabledDate(time: Date) {
        return self.disabledDate(time)
      }
    }
  }

  disabledDate(time: Date): any {
    if (this.filter.dateStart) return time.getTime() < this.filter.dateStart + 8.64e7
  }

  @Watch('filter.search')
  handleSearch(keyword: string): void {
    this.$emit('search', keyword)
  }

  remoteMethod(key: string): void {
    this.resetQuery()
    this.queryUser.search = trim(key)
    employeeReq.all(this.queryUser).then(res => {
      this.members = res
    })
  }

  resetQuery(): void {
    this.queryUser = {
      i: 0,
      ps: 20,
      search: '',
      groupCode: 'INSURANCE',
      statusAccount: 3
    }
  }

  handleChangeDate(): void {
    if (this.filter.dateStart && this.filter.dateEnd) {
      const dateFd = new Date(this.filter.dateStart)
      const dateTd = new Date(this.filter.dateEnd)
      //@ts-ignore
      let dateTimeFd = this.$options.filters.formatDateTime(dateFd)
      const fd = dateTimeFd.split('/').reverse().join('-')
      //@ts-ignore
      let dateTimeTd = this.$options.filters.formatDateTime(dateTd)
      const td = dateTimeTd.split('/').reverse().join('-')

      this.$emit('filterDate', { fd, td })
    }
    if (!this.filter.dateStart && !this.filter.dateEnd) {
      const fd = null
      const td = null
      this.$emit('filterDate', { fd, td })
    }
  }

  handleShowPopper(): void {
    if (!this.members.length) {
      this.handleGetMembers()
    }
  }

  handleGetMembers(): void {
    employeeReq.all(this.queryUser).then(res => {
      this.members = [...this.members, ...res]
    })
  }

  handleChangeCheckBox(val: Array<string>): void {
    this.$emit('filterStatus', val)
  }

  handleChangeUser(val: Array<number>): void {
    this.$emit('filterUser', val)
  }

  load(): void {
    this.queryUser.i += 20
    this.handleGetMembers()
  }
}
