











































import { fileAccept } from '../../config'
import { Component, Mixins } from 'vue-property-decorator'
import PopupMixin from '@/mixins/popup'
import ImportRepository from '@/services/repositories/import/index'
import ProgramRepository from '@/services/repositories/program'
import getRepository from '@/services'
import { debounce, trim } from 'lodash'

const importReq: ImportRepository = getRepository('import')
@Component({ components: {} })
export default class PopupUploadCTTD extends Mixins(PopupMixin) {
  programReq!: ProgramRepository

  listCttd: Array<Record<string, any>> = []
  form: Record<string, any> = {
    year: new Date().getFullYear() + '',
    month: new Date().getMonth() + 1
  }
  query: Record<string, any> = {
    pageNo: 1,
    pageSize: 20,
    search: ''
  }
  fileAccept: Array<string> = fileAccept
  listProgram: Array<Record<string, any>> = []

  created(): void {
    this.programReq = getRepository('program', {
      districtId: this.districtId
    })
  }

  handleOpen(): void {
    this.getListCttd('')
  }

  loadMoreCttd(): void {
    this.query.pageNo += 1
    const a = debounce(this.getListCttd, 200)
    a(this.query.search, true)
  }

  remoteCttd(query: string): void {
    this.query.search = trim(query)
    this.query.pageNo = 1
    this.query.pageSize = 20
    const a = debounce(this.getListCttd, 500)
    a(query)
  }

  async getListCttd(search: string, isAdd = false): Promise<any> {
    try {
      const result = await this.programReq.search({
        keywordString: trim(search),
        pageNo: this.query.pageNo,
        pageSize: this.query.pageSize
      })
      if (isAdd) {
        const a = result || []
        this.listCttd = [...this.listCttd, ...a]
      } else {
        this.listCttd = result || []
      }
    } catch (error) {
      this.listCttd = []
    }
  }

  handleCancel(): void {
    this.form = {
      year: new Date().getFullYear() + '',
      month: new Date().getMonth() + 1
    }
    this.handleClose()
  }

  handleClose(): void {
    this.query = {
      pageNo: 1,
      pageSize: 20,
      search: ''
    }
    this.setOpenPopup({
      popupName: 'upload-file-year',
      isOpen: false
    })
  }
  handleChangeFile(file: Record<string, any>): void {
    this.uploadFile(file)
  }
  uploadFile(file: Record<string, any>): void {
    const formData = new FormData()
    const type = this.$route.name === 'importCbnv' ? 'PROGRAM_MEMBER' : 'PROGRAM_BRANCH'
    let year: any = new Date(this.form.year).getFullYear()
    if (file.status === 'ready') {
      formData.append('file', file.raw)
      formData.append('name', file.name)
      formData.append('type', type)
      formData.append('year', year)
      formData.append('month', this.form.month)
      formData.append('programId', this.form.programId)
      const message = this.$message({
        message: 'Đang tải file lên',
        type: 'success',
        duration: 0
      })
      //@ts-ignore
      this.$message.close()
      importReq
        .uploadFile(this.districtId, formData)
        .then(() => {
          message.close()
          this.$message.success('Tải lên thành công')
          this.$emit('apply')
          this.handleCancel()
        })
        .catch(error => {
          message.close()
          this.handleClose()
          if (error.errorCode === 429) {
            this.$message.error('Không upload được file khi hệ thống đang cập nhật dữ liệu. Vui lòng thử lại sau ít phút')
          } else {
            this.$message.error('Tải lên thất bại')
          }
        })
    }
  }
}
