

































































import { PaginationInterface } from '@/interface'
import { Component, Mixins, Watch } from 'vue-property-decorator'
import PopupMixin from '@/mixins/popup'
import DistrictMixin from '@/mixins/commonMixin'
import getRepository from '@/services'
import { namespace } from 'vuex-class'
import ImportRepository from '@/services/repositories/import/index'
import ImportFilter from '../components/filter/Filter.vue'
import ImportFileDetail from '../components/popup/PopupDetailCTTD.vue'
import UploadFile from '../components/popup/PopupUploadCttd.vue'
import { trim, debounce } from 'lodash'
const beUser = namespace('beUser')
const importReq: ImportRepository = getRepository('import')
@Component({ components: { ImportFilter, ImportFileDetail, UploadFile } })
export default class ImportPageCTTD extends Mixins(PopupMixin, DistrictMixin) {
  data: Array<Record<string, any>> = []
  isLoadingButton = false
  @beUser.State('user') user!: any
  apiImport?: ImportRepository
  query: PaginationInterface = {
    page: 1,
    limit: 20,
    total: 0 // chưa có api total nên fix cứng
  }
  filter = {
    search: '',
    fd: '',
    td: '',
    status: '',
    userIds: ''
  }

  detailFile = {}
  isError = false
  isLoading = false
  params: Record<string, any> = {
    type: 'kpi-year'
  }
  isSpace = false

  @Watch('$route.name') handleRouteNameChange(): void {
    this.params = {
      page: 1,
      limit: 20,
      total: 0
    }
    this.filter = {
      search: '',
      fd: '',
      td: '',
      status: '',
      userIds: ''
    }
    this.init()
  }

  mounted(): void {
    this.init()
  }
  get getIndex(): number {
    return this.query.limit * (this.query.page - 1) + 1
  }
  // lay ra list du lieu import
  async init(): Promise<void> {
    this.isLoading = true
    const temp = {
      ...this.query,
      search: this.filter.search ? this.filter.search : null,
      fd: this.filter.fd ? this.filter.fd : null,
      td: this.filter.td ? this.filter.td : null,
      status: this.filter.status ? this.filter.status : null,
      userIds: this.filter.userIds ? this.filter.userIds : null,
      total: null,
      type: this.$route.name === 'importCbnv' ? 'PROGRAM_MEMBER' : 'PROGRAM_BRANCH'
    }
    const result = await importReq.getData(this.districtId, temp)
    this.data = result.files
    this.query.total = result.total
    this.isLoading = false
  }
  UploadFileYear(): void {
    this.setOpenPopup({
      popupName: 'upload-file-year',
      isOpen: true
    })
  }
  handleSearch = debounce((key: string) => {
    this.resetQuery()
    if (this.isLoading) {
      return
    } else {
      this.filter.search = trim(key)
      this.init()
    }
  }, 500)
  created(): void {
    this.apiImport = getRepository('import', {
      districtId: this.user?.districts?.[0].id
    })
  }
  async handleOpenExportFile(): Promise<void> {
    this.isLoadingButton = true
    let response: any

    const temp = {
      type: this.$route.name === 'importCbnv' ? 'PROGRAM_MEMBER' : 'PROGRAM_BRANCH'
    }
    await this.apiImport
      // eslint-disable-next-line no-undef
      ?.spamleFile(this.districtId, temp)
      .then(data => {
        response = data.data
        const a = document.createElement('a')
        a.style.display = 'none'
        a.href = response
        document.body.appendChild(a)
        a.click()
        document.body.removeChild(a)
        this.isLoadingButton = false
      })
      .catch(err => {
        console.log(err)
        this.isLoadingButton = false
      })
  }
  handleFilterDate(date: Record<string, any>): void {
    this.resetQuery()
    this.filter.fd = date.fd
    this.filter.td = date.td
    this.init()
  }
  handleFilterStatus(status: Array<string>): void {
    this.resetQuery()
    this.filter.status = status.join(',')
    this.init()
  }

  handleFilterUser(list: Array<number>): void {
    this.resetQuery()
    this.filter.userIds = list.join(',')
    this.init()
  }

  handleSizeChange(size: number): void {
    this.query.limit = size
    this.init()
  }
  handleCurrentChange(page: number): void {
    this.query.page = page
    this.init()
  }

  handleGetDetail(row: Record<string, any>): void {
    this.detailFile = row.row
    this.isError = row.row.status === 'FAIL'
    if (row.row.status !== 'IN_PROGRESS' && row.row.status !== 'NEW' && row.row.status !== 'SUCCESS') {
      this.setOpenPopup({
        popupName: 'popup-detail-cttd',
        isOpen: true
      })
    } else {
      this.$message.error('File đang xử lý, vui lòng đợi')
    }
  }

  resetQuery(): void {
    this.query.page = 1
    this.query.limit = 20
  }

  hasWhiteSpace(text: string): void {
    if (text.indexOf(' ') >= 0) {
      this.isSpace = true
    }
  }
}
